html{
    box-sizing: border-box;
}

*, *::after, *::before{
    box-sizing: inherit;
}

body{
    margin: 0;
    font-family: 'Roboto Slab', 'Roboto', serif;
    font-size: clamp(0.75rem, calc(0.75rem + 1vw), 1.25rem);
}

a{
    text-decoration: none;
    color: inherit;
}

h1,h2,h3,h4{
    font-family: 'Roboto', sans-serif;
}